@use 'sass:color';
@use '../util' as *;
$primary-color: hsl(26, 100%, 55%);

.main {
    @include breakpoint(small) {
        flex-direction: column;
    }

    @include breakpoint(large) {
        margin-top: 10vh;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        gap: 100px;
    }

    //image-section styles

    &__section--images {
       display: flex;
       flex-direction: column;
       gap: 0px;

       @include breakpoint(large) {
            gap: 25px;
       }
    }

    &__thumbnails {
        @include breakpoint(large) {
            display: flex;
            justify-content: space-between;
        }
        
        @include breakpoint-down(large) {
            display: none;
        }
    }

    picture {
        line-height: 0;
        border-radius: 15px;
    }

    &__image {
        border-radius: 15px;
        cursor: pointer;
        line-height: 0;

        &--big {
            border-radius: 0;
            display: none;
            
            @include breakpoint(large) {
                border-radius: 25px;
                display: block;
            }
        }

        &--thumb {
            &:hover, &:focus {
                border: 3px solid var(--primary-orange);
                opacity: 0.8; 
                outline: none;
            }
        }
    }

    //slide gallery on <= medium screen-width

    &__add-gallery {
        display: block;
        width: 100vw;

        @include breakpoint(large) {
            display: none;
        }

        img {
            min-width: 20vh;
            width: 50vw;
            height: auto;
            display: block;

            @include breakpoint-down(small) {
                width: 100vw;
            }
        }
    }

    &__slide-container {
        list-style: none;
        display: flex;
        flex-flow: row nowrap;
        overflow: hidden;
        height: auto;
    }

    &__slide {
        display: inline-block;
        flex: 1 1 auto;
    }

    &__slide-arrow {
        position: absolute;
    }

    //product-info section

    &__section--product-info {
        display: flex;
        flex-flow: column nowrap;
        gap: 12px;
    }


    &__h2 {
        font-size: 16px;
        color: var(--primary-orange);
        text-transform: uppercase;
        margin: 60px 0 15px 0;
    }

    &__h3 {
        font-size: 44px;
        margin-bottom: 30px;
    }

    &__newline {
        display: block;
    }

    &__nowrap {
        white-space: nowrap;
    }

    &__p {
        color: var(--dark-graish-blue);
        margin-bottom: 20px;
        font-size: 17px;
        letter-spacing: 0.5px;
        line-height: 1.5;
    }

    &__discount-price {
        display: flex;
        align-items: center;
        gap: 4%;
    }

    &__price {
        font-weight: 600;
        font-size: 34px;
    }

    &__discount {
        color: var(--primary-orange);
        background-color: var(--primary-pale-orange);
        padding: 4px 6px;
        border-radius: 20%;
        font-weight: 600;
    }

    &__original-price {
        font-weight: 600;
        color: var(--dark-graish-blue);
        opacity: 0.6;
        font-size: 1.2rem;
        margin-bottom: 15px;
    }

    &__counter {
        background-color: var(--light-graish-blue);
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 180px;
        padding: 5px 12px;
        border-radius: 10px;
    }

    &__quantity {
        background-color: inherit;
        text-shadow: 1px 1px 2px black;
        border: none;
        outline: none;
        text-align: center;
        font-size: 16px;
        padding: 10px;
        width: 100px;
        font-weight: 400;
        font-size: 20px;
        appearance: none;


        // omitting arrow buttons
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            display: none;
        }

        &[type=number] {
            -moz-appearance: textfield;
            appearance: textfield;
        }
    }

    &__button {
        border: none;
        background-color: inherit;
        cursor: pointer;

        &--plus {
            margin-top: 5px;
        }
    }

    &__wrapper {
        display: flex;
        gap: 25px;
    }

    &__cta {
        background-color: var(--primary-orange);
        color: var(--white);
        padding: 10px 20px;
        border-radius: 15px;
        flex-grow: 1;
        display: flex;
        justify-content: center;
        gap: 20px;
        align-items: center;
        height: 65px;

        &:hover, &:focus {
            background-color: lighten($primary-color, 15%);
        }
    }

    &__cart {
        filter: invert(100%) sepia(0%) saturate(2%) hue-rotate(316deg) brightness(111%) contrast(101%);
    }

    &__cta-text {
        font-size: 18px;
        letter-spacing: 1.2px;
    }
}